import React from 'react';
import { Button, Modal, Form, Col, InputGroup } from 'react-bootstrap';

import { Search } from 'react-feather';

class MarketsRegistrationModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Stall Registration
                        </Modal.Title>
                    </Modal.Header>

                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Row>
                                <Form.Group as={Col} md="3" controlId="MarketNo">
                                    <Form.Label>Registration Number</Form.Label>
                                    <Form.Control type="text" 
                                        placeholder="Enter Market number" 
                                        value={this.props.MarketNo} 
                                        onChange={this.props.changeMarketNo} readOnly />
                                </Form.Group>
                                <Form.Group as={Col} md="3" controlId="StallNo">
                                    <Form.Label>Stall No</Form.Label>
                                    <Form.Control type="number" 
                                        placeholder="Enter Stall No" 
                                        value={this.props.StallNo} 
                                        onChange={this.props.changeStallNo}
                                        required
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please Enter Stall Number.
                                    </Form.Control.Feedback>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="MarketNames">
                                    <Form.Label>Maket Name</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.MarketName}
                                            placeholder="Search Market Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowMarketName}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Market Name.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                          
                            <Form.Row>
                            <Form.Group as={Col} md="6" controlId="ModuleType">
                                    <Form.Label>Module Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.ModuleDescription}
                                            placeholder="Search Module Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowModuleSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Module Type.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>

							<Form.Group as={Col} md="6" controlId="FeeCategory">
                                <Form.Label>Revenue Type</Form.Label>
                                <InputGroup>
                                    <Form.Control
                                        type="text"
                                        value={this.props.FeeCategoryName}
                                        placeholder="Search Revenue Type"
                                        aria-describedby="inputGroupPrepend"
                                        required
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowFeeCategorySearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                    <Form.Control.Feedback type="invalid">
                                            Please select Revenue Type.
                                        </Form.Control.Feedback>
                                </InputGroup>
                            </Form.Group>
                            </Form.Row>

                            <Form.Row>
                            <Form.Group as={Col} md="6" controlId="Fee">
                                    <Form.Label>Revenue Name</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.FeeName}
                                            placeholder="Search Revenue Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowFees}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Revenue Name.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="SystemStatusType">
                                    <Form.Label>System Status Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.SystemStatusTypeDescription}
                                            placeholder="Search System Status Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Status.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>

                            <Form.Row>
                            <Form.Group as={Col} md="6" controlId="CitizenID">
                                <Form.Label>Client</Form.Label>
                                    <InputGroup>
                                            <Form.Control
                                                type="text"
                                                value={this.props.ClientName}
                                                placeholder="Search Client Name"
                                                aria-describedby="inputGroupPrepend"
                                                required
                                            />
                                            <InputGroup.Prepend>
                                                <Button variant="info" size="sm" onClick={this.props.handleShowClientRegistrationSearch}>
                                                    <Search />
                                                </Button>
                                            </InputGroup.Prepend>
                                            <Form.Control.Feedback type="invalid">
                                                Please select Client Name.
                                            </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="PostalAddress">
                                    <Form.Label>Postal Address</Form.Label>
                                        <Form.Control type="text" placeholder="Enter Postal Address" value={this.props.ClientPostalAddress}
                                        onChange={this.props.changeClientPostalAddress}  readOnly />
                                </Form.Group>
                            </Form.Row>

                            <Form.Row>
                            <Form.Group as={Col} md="6" controlId="DocumentNumber">
                                <Form.Label>Document Number</Form.Label>
                                    <Form.Control type="text" placeholder="Enter Document Number" value={this.props.ClientDocumentNumber}
                                    onChange={this.props.changeClientDocumentNumber}  readOnly />
                                </Form.Group>
                            <Form.Group as={Col} md="6" controlId="MobileNumber">
                                <Form.Label>Tele Phone Number</Form.Label>
                                    <Form.Control type="text" placeholder="Enter Mobile Number" value={this.props.ClientMobileNumber}
                                    onChange={this.props.changeMobileNumber}  readOnly />
                            </Form.Group> 
                            </Form.Row>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Stall Registration
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description_del">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.MarketNo} onChange={this.props.changeMarketNo} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default MarketsRegistrationModal;